
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
body{
  background-color: #EAEAEA;
}
a:focus,a:hover{
    color: #f10214;
    text-decoration: none;
}
.header{
    height: 48px;
    line-height: 48px;
    background: rgba(0,0,0,0.6);
    color: #fff;
}
.icon{
    width: 20px;
    height: 20px;
    margin-right: 5px;
}
.header-home{
    padding: 0 32px;
    float: left;
    color: #fff;
}
.header-user{
    float: right;
    margin-top: 5px;
    margin-right: 10px;
    height: 34px;
    line-height: 34px;
}
.header-userName{
    color: #f10214;
}
.cartIcon{
    width: 15px;
    height: 15px;
}
.header-cart span{
    position: absolute;
    top: 12px;
    right: 15px;
    display: inline-block;
    padding: 1px;
    font-size: 12px;
    line-height: 12px;
    letter-spacing: -1px;
    color: #E3E4E5;
    background-color: #da1026;
    border-radius: 7px;
    min-width: 12px;
    font-family: tahoma,arial;
    text-align: center;
}
.container{
    width: 100%;
}
.footer{
    text-align: center;
    margin-top: 100px;
    display: inline-block;
    bottom:0px;
}
@media (max-width: 960px){
.header-home{
        padding: 5px;
}
.mod_help_list div{
        display: none;
}
