}
.footer div{
    display: inline-block;
    margin-bottom: 5px;
}
h4{
    display: block;
    font-weight: bold;
}
li{
    list-style: none;
    display: list-item;
    text-align: left;
}
a{
    color: #666;
    text-decoration: none;
}
.footer-a a{
    padding-right: 20px;
}
.footinfo{
    color: #666;
    margin-top: 35px;
}

